import { CollectionType } from "utils/interfaces";
import { getFileExtension } from "helper";
import store from "store";
import { COLLECTION_IDS } from "utils/constants";
import { GetCollectionOptions } from "utils/interfaces";
import { RoleDocument } from "utils/types";
import FilesManager from "./FilesManager";

class DataProvider {
  apiUrl: string;
  filesManager: FilesManager;

  constructor(apiUrl: string, filesManager: FilesManager) {
    this.apiUrl = apiUrl;
    this.filesManager = filesManager;
  }

  getAuthHeader = () => {
    const user = store.getState().user;
    const token = user ? user.token : null;
    return "Bearer " + token;
  };

  wrapRequest(url: string, method: string, body?: any) {
    try {
      return fetch(url, {
        method: method,
        headers: { Authorization: this.getAuthHeader() },
        body: body ? JSON.stringify(body) : undefined,
      }).then((x) => x.json());
    } catch (error) {
      return Promise.resolve({ error });
    }
  }

  addDocument = (collectionId: string, doc: { [index: string]: any } | File) =>
    this.wrapRequest(this.apiUrl + `/api/${collectionId}`, "POST", doc);

  deleteDocument = (collectionId: string, docId: string) =>
    this.wrapRequest(this.apiUrl + `/api/${collectionId}/${docId}`, "DELETE");

  getDocument = (collectionId: string, docId: string) =>
    this.wrapRequest(
      this.apiUrl + `/api/${collectionId}${docId ? `/${docId}` : ""}?populateRef=false`,
      "GET"
    );

  updateDocument = (collectionType: string, docId: string, doc: { [index: string]: any }) =>
    this.wrapRequest(this.apiUrl + `/api/${collectionType}/${docId}`, "PUT", doc);

  getCollection = (
    {
      collectionId,
      orderBy,
      startAt,
      startAfter,
      where,
      limit = 10,
      populateRef = true,
    }: GetCollectionOptions,
    signal?: AbortSignal
  ) => {
    const url = `${this.apiUrl}/api/${collectionId}?limit=${limit}&populateRef=${populateRef}${
      orderBy ? `&orderBy=${orderBy}` : ""
    }${startAt ? `&startAt=${startAt}` : startAfter ? `&startAfter=${startAfter}` : ""}${
      where ? `&where=${where}` : ""
    }`;
    return this.wrapRequest(url, "GET");
  };

  createCollection = async (type: CollectionType) => {
    try {
      const finalType = {
        ...type,
        fields: [
          ...type.fields,
          {
            id: "createdAt",
            type: "date",
            displayOnTable: true,
            isDefault: true,
          },
          {
            id: "modifiedAt",
            type: "date",
            displayOnTable: true,
            isDefault: true,
          },
          {
            id: "docId",
            type: "string",
            displayOnTable: true,
            isDefault: true,
          },
        ],
      };

      let doc = await this.addDocument(COLLECTION_IDS.COLLECTION_TYPES, finalType);

      if (!doc.error && doc.docId) {
        const roles: RoleDocument[] = await this.getCollection({
          collectionId: COLLECTION_IDS.ROLES,
          limit: 1000,
        });

        roles.forEach((x) =>
          this.updateDocument(COLLECTION_IDS.ROLES, x.docId, {
            ...x,
            permissions: { ...x.permissions, [doc.docId]: x.defaultPermissions },
          })
        );
      }

      return doc;
    } catch (error: any) {
      return Promise.resolve({ error: error.toString() });
    }
  };

  uploadFile = async (file: File) => {
    try {
      const res = await this.filesManager.upload(file);

      if ("error" in res) {
        return res;
      } else {
        const fileDetails = {
          name: file.name,
          size: file.size,
          url: res.downloadUrl,
          storagePath: res.fileName,
          type: getFileExtension(file),
        };
        return this.addDocument(COLLECTION_IDS.MEDIA, fileDetails);
      }
    } catch (error: any) {
      return Promise.resolve({ error: error.toString() });
    }
  };

  deleteFile = async (storagePath: string, docId: string) => {
    try {
      this.filesManager.delete(storagePath);
      return this.deleteDocument(COLLECTION_IDS.MEDIA, docId);
    } catch (error) {
      return { error };
    }
  };
}

export default DataProvider;
